                'bg-red-status':this.visualTest.visual_test_result == 'Reject'}"
              >
                <label>{{this.visualTest.visual_test_result}}</label>
              </div>
            </div>
            <div class="section-label" style="grid-column: span 2;">
              <label>Latest Load Test</label>
            </div>
            <div class="form-item" v-for="item in loadTestInfo" :key="item.index">
              <div class="form-item-label">
                <label>{{ item.desc }}</label>
              </div>
              <div class="form-item-value">
                <label>{{ item.value }}</label>
              </div>
            </div>
            <div class="form-item" style="grid-column: span 2; grid-row: span 2; ">
              <div class="form-item-label">
                <label>Load Test Result</label>
              </div>
              <div
                class="form-item-value"
                :class="{'bg-green-status':this.loadTest.load_test_result == 'Accept',
                'bg-red-status':this.loadTest.load_test_result == 'Reject'}"
              >
                <label>{{this.loadTest.load_test_result}}</label>
              </div>
            </div>
            <div class="section-label" style="grid-column: span 2;">
              <label>Latest MPI</label>
            </div>
            <div class="form-item" v-for="item in mpiInfo" :key="item.index">
              <div class="form-item-label">
                <label>{{ item.desc }}</label>
              </div>
              <div class="form-item-value">
                <label>{{ item.value }}</label>
              </div>
            </div>
            <div class="form-item" style="grid-column: span 2; grid-row: span 2; ">
              <div class="form-item-label">
                <label>MPI Result</label>
              </div>
              <div
                class="form-item-value"
                :class="{'bg-green-status':this.mpi.mpi_result == 'Accept',
                'bg-red-status':this.mpi.mpi_result == 'Reject'}"
              >
                <label>{{this.mpi.mpi_result}}</label>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="report-sheet">
        <div class="report-container">
          <div class="sheet-body">
            <div>
              <div class="section-label">
                <label>overview picture</label>
              </div>

              <div class="form-item-picture-log">
                <div class="img-box">
                  <div class="btn-panel">
                    <v-ons-toolbar-button
                      class="pic-toolbar-btn"
                      v-on:click="PREVIEW_PIC(infoLifting.overview_img_path)"
                      v-if="infoLifting.overview_img_path"
                    >
                      <i class="las la-eye"></i>
                    </v-ons-toolbar-button>
                    <v-ons-toolbar-button
                      class="pic-toolbar-btn"
                      v-on:click="DELETE_PIC(infoLifting.overview_img_path, 1)"
                      v-if="infoLifting.overview_img_path"
                    >
                      <i class="las la-trash"></i>
                    </v-ons-toolbar-button>
                    <v-ons-toolbar-button>
                      <label
                        class="pic-toolbar-btn"
                        for="pic-upload-btn"
                        v-if="!infoLifting.overview_img_path"
                      >
                        <i class="las la-plus"></i>
                      </label>
                    </v-ons-toolbar-button>
                  </div>
                  <img
                    :src="baseURL + infoLifting.overview_img_path"
                    v-if="infoLifting.overview_img_path"
                  />
                  <input
                    type="file"
                    id="pic-upload-btn"
                    style="display: none"
                    ref="pic_upload_ov"
                    @change="UPLOAD_PIC(1)"
                  />

                  <div class>
                    <i class="las la-image" v-if="!infoLifting.overview_img_path"></i>
                    <label v-if="!infoLifting.overview_img_path">No Image</label>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="report-sheet">
        <div class="report-container">
          <div class="sheet-body">
            <div>
              <div class="section-label">
                <label>name plate picture</label>
              </div>

              <div class="form-item-picture-log">
                <div class="img-box">
                  <div class="btn-panel">
                    <v-ons-toolbar-button
                      class="pic-toolbar-btn"
                      v-on:click="PREVIEW_PIC(infoLifting.name_plate_img_path)"
                      v-if="infoLifting.name_plate_img_path"
                    >
                      <i class="las la-eye"></i>
                    </v-ons-toolbar-button>
                    <v-ons-toolbar-button
                      class="pic-toolbar-btn"
                      v-on:click="DELETE_PIC(infoLifting.name_plate_img_path, 2)"
                      v-if="infoLifting.name_plate_img_path"
                    >
                      <i class="las la-trash"></i>
                    </v-ons-toolbar-button>
                    <v-ons-toolbar-button v-if="!infoLifting.name_plate_img_path">
                      <label
                        class="pic-toolbar-btn"
                        for="pic-upload-btn-2"
                        v-if="!infoLifting.name_plate_img_path"
                      >
                        <i class="las la-plus"></i>
                      </label>
                    </v-ons-toolbar-button>
                  </div>
                  <img
                    :src="baseURL + infoLifting.name_plate_img_path"
                    v-if="infoLifting.name_plate_img_path"
                  />
                  <input
                    type="file"
                    id="pic-upload-btn-2"
                    style="display: none"
                    ref="pic_upload_np"
                    @change="UPLOAD_PIC(2)"
                  />
                  <i class="las la-image" v-if="!infoLifting.name_plate_img_path"></i>
                  <label v-if="!infoLifting.name_plate_img_path">No Image</label>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div v-if="tabCurrent=='doc'">
      <tableGeneralDoc />
    </div>
    <div v-if="tabCurrent=='insp'">
      <tableInsp />
    </div>
    <div v-if="tabCurrent=='const'">
      <tableConst />
    </div>

    <contentLoading text="Loading, please wait..." v-if="isLoading == true" color="#fc9b21" />
    <previewImage :imageURL="previewImg" v-if="previewImg" @close-preview="PREVIEW_PIC_CLOSE()" />
  </div>
</template> 

<script>
//API
import axios from "/axios.js";
import moment from "moment";

//Components
import VueTabsChrome from "vue-tabs-chrome";
import contentLoading from "@/components/app-structures/app-content-loading.vue";
import previewImage from "@/components/image-preview.vue";
import tableGeneralDoc from "./table-generalDoc.vue";
import tableConst from "./table-const.vue";
import tableInsp from "./table-insp.vue";

//DataGrid

export default {
  name: "LiftingInformation",
  components: {
    VueTabsChrome,
    contentLoading,
    previewImage,
    tableGeneralDoc,
    tableConst,
    tableInsp
  },
  created() {
    this.$store.commit("UPDATE_CURRENT_PAGENAME", {
      subpageName: "Lifting Information",
      subpageInnerName: null
    });
    this.FETCH_INFO();
    this.FETCH_LATEST_VISUAL_TEST();
    this.FETCH_LATEST_LOAD_TEST();
    this.FETCH_LATEST_MPI();
  },
  data() {
    return {
      infoLifting: {},
      visualTest: {},
      loadTest: {},
      mpi: {},
      file_pic_upload: "",
      tabCurrent: "info",
      tabs: [
        {
          label: "General Information",
          key: "info",
          closable: false
        },
        {
          label: "Construction",
          key: "const",
          closable: false
        },
        {
          label: "Inspection",
          key: "insp",
          closable: false
        },
        {
          label: "General Documents",
          key: "doc",
          closable: false
        }
      ],
      previewImg: "",
      isLoading: false
    };
  },
  computed: {
    generalInfo() {
      var info = [
        {
          desc: "Tag No.",
          value: this.infoLifting.tag_no
        },
        {
          desc: "swl main",
          value: this.infoLifting.swl_main
        },
        {
          desc: "Serial No.",
          value: this.infoLifting.serial_no
        },
        {
          desc: "swl aux",
          value: this.infoLifting.swl_aux
        },
        {
          desc: "Site",
          value: this.infoLifting.id_site
        },
        {
          desc: "Equipment User",
          value: this.infoLifting.eq_user
        },
        {
          desc: "Part line",
          value: this.infoLifting.part_line
        },
        {
          desc: "Equipment Type",
          value: this.infoLifting.id_eq_type
        },
        {
          desc: "Model",
          value: this.infoLifting.model
        },
        {
          desc: "Equipment Owner",
          value: this.infoLifting.eq_owner
        },
        {
          desc: "License Plate No.",
          value: this.infoLifting.license_plate_no
        },
        {
          desc: "License Plate Province",
          value: this.infoLifting.license_plate_province
        }
        // {
        //   desc: "P&ID No.",
        //   value: this.infoLifting.pid_no
        // },
        // {
        //   desc: "Internal Coating Type",
        //   value: this.infoLifting.internal_coating_type
        // },
        // {
        //   desc: "Drawing No.",
        //   value: this.infoLifting.drawing_no
        // },
        // {
        //   desc: "External Coating",
        //   value: this.infoLifting.external_coating
        // },
        // {
        //   desc: "Construction Material",
        //   value: this.infoLifting.material_of_const
        // },
        // {
        //   desc: "External Coating Type",
        //   value: this.infoLifting.external_coating_type
        // },
        // {
        //   desc: "Inservice Date",
        //   value: this.infoLifting.next_insp_date
        //     ? moment(this.infoLifting.inservice_date).format("LL")
        //     : null
        // },
        // {
        //   desc: "Cladding Material",
        //   value: this.infoLifting.cladding_material
        // },
        // {
        //   desc: "Internal Lining",
        //   value: this.infoLifting.internal_lining
        // },
        // {
        //   desc: "Cladding Material Type",
        //   value: this.infoLifting.cladding_material_type
        // },
        // {
        //   desc: "Code Stamp",
        //   value: this.infoLifting.code_stamp
        // },
        // {
        //   desc: "Equipment Type",
        //   value: this.infoLifting.equipment_type
        // },
        // {
        //   desc: "RT",
        //   value: this.infoLifting.RT
        // },
        // {
        //   desc: "Equipment Desc",
        //   value: this.infoLifting.equipment_desc
        // },
        // {
        //   desc: "Tube Material",
        //   value: this.infoLifting.tube_material
        // },
        // {
        //   desc: "Tube Outside Diameter (mm)",
        //   value: this.infoLifting.tube_OD_mm
        // },
        // {
        //   desc: "Tube Service",
        //   value: this.infoLifting.tube_service
        // },
        // {
        //   desc: "Tube Nominal Thickness (mm)",
        //   value: this.infoLifting.tube_t_nom
        // },
        // {
        //   desc: "Tube Type",
        //   value: this.infoLifting.tube_type
        // },
        // {
        //   desc: "Tube Quantity",
        //   value: this.infoLifting.tube_quantity
        // },
        // {
        //   desc: "Tube Length (m)",
        //   value: this.infoLifting.tube_length
        // },
        // {
        //   value: null
        // }
      ];
      return info;
    },
    visualTestInfo() {
      const info = [
        {
          desc: "Certificate No.",
          value: this.visualTest.cert_no
        },
        {
          desc: "Visual Test Date",
          value: this.visualTest.visual_test_date
            ? moment(this.visualTest.visual_test_date).format("LL")
            : "N/A"
        },
        {
          desc: "Visual Test By",
          value: this.visualTest.visual_by
        },
        {
          desc: ""
        }
      ];
      return info;
    },
    loadTestInfo() {
      const info = [
        {
          desc: "Certificate No.",
          value: this.loadTest.cert_no
        },
        {
          desc: "Load Test Date",
          value: this.loadTest.load_test_date
            ? moment(this.loadTest.load_test_date).format("LL")
            : "N/A"
        }
      ];
      return info;
    },
    mpiInfo() {
      const info = [
        {
          desc: "Certificate No.",
          value: this.mpi.cert_no
        },
        {
          desc: "MPI Date",
          value: this.mpi.mpi_date
            ? moment(this.mpi.mpi_date).format("LL")
            : "N/A"
        }
      ];
      return info;
    },
    baseURL() {
      var mode = this.$store.state.mode;
      if (mode == "dev") return this.$store.state.modeURL.dev;
      else if (mode == "prod") return this.$store.state.modeURL.prod;
      else return console.log("develpment mode set up incorrect.");
    }
  },
  watch: {
    // watch for changes in the $route object
    // need to do this because when use search in navbar (v-select) the router.push() only change path but not create new component because we use same page so created() will not be called
    $route: {
      handler() {
        // run this logic whenever router is change
        this.FETCH_INFO();
      }
      //immediate: true // call the handler immediately when the component is created
    }
  },
  methods: {
    FETCH_INFO() {
      console.log("==> FETCH Lifting INFO: ");
      this.isLoading = true;
      const id_tag = this.$route.params.id_tag;

      axios({
        method: "get",
        url: "LiftingInfo/" + id_tag,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            this.infoLifting = res.data;
            console.log(this.infoLifting);
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    FETCH_LATEST_VISUAL_TEST() {
      this.isLoading = true;
      axios({
        method: "get",
        url:
          "LiftingTest/get-lifting-last-visual-test?id_tag=" +
          this.$route.params.id_tag,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            this.visualTest = res.data[0];
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    FETCH_LATEST_LOAD_TEST() {
      this.isLoading = true;
      axios({
        method: "get",
        url:
          "LiftingTest/get-lifting-last-load-test?id_tag=" +
          this.$route.params.id_tag,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            this.loadTest = res.data[0];
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    FETCH_LATEST_MPI() {
      this.isLoading = true;
      axios({
        method: "get",
        url:
          "LiftingTest/get-lifting-last-mpi?id_tag=" +
          this.$route.params.id_tag,
        headers: {
          Authorization: "Bearer " + JSON.parse(localStorage.getItem("token"))
        },
        data: {}
      })
        .then(res => {
          if (res.status == 200 && res.data) {
            this.mpi = res.data[0];
          }
        })
        .catch(error => {
          console.log(error);
        })
        .finally(() => {
          this.isLoading = false;
        });
    },
    UPLOAD_PIC(pic_type) {
      this.isLoading = true;
      const id_tag = this.$route.params.id_tag;
      if (pic_type == 1) {
        this.file_pic_upload = this.$refs.pic_upload_ov.files[0];
      } else {
        this.file_pic_upload = this.$refs.pic_upload_np.files[0];
      }
      if (
        (this.file_pic_upload && this.file_pic_upload.type == "image/jpeg") ||
        this.file_pic_upload.type == "image/png"
      ) {
        if (this.file_pic_upload.size < 20000000) {
          axios({
            method: "post",
            url: "/Lifting/upload-pic",
            headers: {
              "Content-Type": "multipart/form-data",
              Authorization:
                "Bearer " + JSON.parse(localStorage.getItem("token"))
            },
            data: {
              id: id_tag,
              file: this.file_pic_upload,
              type: pic_type
            }
          })
            .then(res => {
              if (res.status == 200) {
                this.FETCH_INFO();
                console.log("==> Lifting INFO IMG UPLOAD: DONE");
                this.isLoading = false;
              }
            })
            .catch(error => {
              this.isLoading = false;
              this.$ons.notification.alert(
                error.code + " " + error.response.status + " " + error.message
              );
            })
            .finally(() => {});
        } else {
          this.$ons.notification.alert("File size too large. (20 MB max)");
          const file = document.getElementById("pic-upload-btn");
          file.val = "";
          this.isLoading = false;
        }
      } else {
        this.$ons.notification.alert(
          "Incorrect filetype. <br/> Only JPEG/PNG file can be uploaded."
        );
        const file = document.getElementById("pic-upload-btn");
        file.val = "";
        this.isLoading = false;
      }
    },
    DELETE_PIC(file_path, pic_type) {
      this.$ons.notification.confirm("Confirm delete?").then(res => {
        if (res == 1) {
          this.isLoading = true;
          const id_tag = this.$route.params.id_tag;
          axios({
            method: "delete",
            url: "/Lifting/delete-pic",
            headers: {
              Authorization:
                "Bearer " + JSON.parse(localStorage.getItem("token"))
            },
            data: {
              id: id_tag,
              file_path: file_path,
              type: pic_type
            }
          })
            .then(res => {
              if (res.status == 200) {
                this.FETCH_INFO();
                console.log("==> Lifting INFO IMG DELETE: DONE");
                this.isLoading = false;
              }
            })
            .catch(error => {
              this.isLoading = false;
              this.$ons.notification.alert(
                error.code + " " + error.response.status + " " + error.message
              );
            })
            .finally(() => {
              this.FETCH_INFO();
            });
        }
      });
    },
    PREVIEW_PIC(img) {
      if (img) {
        this.previewImg = img;
      }
    },
    PREVIEW_PIC_CLOSE() {
      this.previewImg = "";
    }
  }
};
</script>

